import React, { Fragment } from "react";
import { profileService } from "../../../services/ProfileService";
import { ProfileCard } from "./ProfileCard";
import { UpdateProfile } from "./UpdateProfile";
import { myFetchPUT } from "../../../services/apiService";
import './profilePage.css';
import { Loader } from "../../partials/Loader";


class ProfilePage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            profile: null
        }
    }

    updateProfilePage = () => {
        return profileService.fetchProfile()
            .then(response => {
                this.setState({ profile: response })
            });
    }

    componentDidMount() {
        this.updateProfilePage()
    }

    updateProfile = (text, image, name) => {
        const { profile } = this.state;
        const changeProfileData = {
            about: text,
            avatarUrl: image,
            name: name,
            email: profile.email,
            aboutShort: profile.aboutShort
        } 

        return myFetchPUT(changeProfileData)
            .then(response => {
                this.updateProfilePage()
            })
    }

    render() {
        const { profile } = this.state;
        return (
            <Fragment>
                {(!profile) ? <Loader /> :
                    (
                        <Fragment>
                            <UpdateProfile profile={profile} updateProfile={this.updateProfile} />
                            <ProfileCard profile={profile} />
                        </Fragment>
                    )}
            </Fragment>
        );
    }
}

export { ProfilePage }


